// FONT DTL PROKYON
// ================================================================

@font-face {
    font-family: DTL Prokyon;
    src: url('/assets/fonts/dtl-prokyon/DTL-Prokyon-Light.ttf') format("truetype");
    font-weight: 100;
    font-style: normal
}

@font-face {
    font-family: DTL Prokyon;
    src: url('/assets/fonts/dtl-prokyon/DTL-Prokyon-Medium.ttf') format("truetype");
    font-weight: 400;
    font-style: normal
}

@font-face {
    font-family: DTL Prokyon;
    src: url('/assets/fonts/dtl-prokyon/DTL-Prokyon-Bold.ttf') format("truetype");
    font-weight: bold;
    font-style: normal
}

body::-webkit-scrollbar {
    background: #092E20;
    height: 8px;
    width: 8px
}

body::-webkit-scrollbar-track {
    border-radius: 5px
}

body::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background: #44B78B
}

.ebook-landing {
    display: table;
    height: 100vh;
    width: 100vw;

    .nav {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;        
        text-align: center;
        background-color: rgba(0, 0, 0, 0.35);

        img {
            padding: 15px;

            @media (max-width: 767px) {
                height: 70px;
            }
        }
    }

    .body {
        background-image: url("/assets/svg/stars-bground.svg"), linear-gradient(to bottom, #075638, #01150d);
        padding: 90px 16px;
        overflow: hidden;
        height: 100vh;
        display: table-cell;
        vertical-align: middle;

        .form-wrapper {
            .form-description {
                padding-right: 40px;

                h1 {
                    font-family: 'Roboto Condensed', sans-serif;
                    font-size: 3rem;
                    color: #FFF;
                    font-weight: 400;
                    margin-bottom: 32px;
                }

                h2 {
                    font-family: 'Roboto Condensed', sans-serif;
                    font-size: 1.7rem;
                    letter-spacing: 1px;
                    text-transform: uppercase;
                    color: #44b77e;
                    font-weight: bold;
                    margin-top: 0;
                }

                p {
                    font-family: 'Lato', sans-serif;
                    font-size: 2rem;
                    color: #eee;
                    margin-bottom: 16px;

                    .django {
                        font-family: 'DTL Prokyon', sans-serif;
                        color: #44b78b;
                        font-weight: bold;
                    }
                }

                ul {
                    list-style: none;
                    li {
                        font-family: 'Lato', sans-serif;
                        font-size: 1.8rem;
                        color: #eee;
                        margin-bottom: 14px;
                    }
                }
            }

            .form-container {
                width: 100%;
                margin: 0 0 0 auto;
                border-radius: 16px;                        
                background: #FFF;
                text-align: center;
                position: relative;
                margin-top: 39px;
                box-shadow: 0px 6px 24px rgba(0, 0, 0, 0.35);            
    
                .form-header {
                    background: #032518;
                    border-radius: 10px 10px 0 0;
                    position: absolute;
                    top: 0;
                    z-index: 999;
                    width: 100%;
    
                    h2 {
                        font-family: 'DTL Prokyon', sans-serif;
                        font-weight: bold;
                        letter-spacing: -0.25px;
                        color: #FFF;
                        font-size: 2rem;
                        padding: 24px 12px;
                        margin: 0;

                        @media (max-width: 767px) {
                            font-size: 1.65rem;
                            padding: 12px;
                            letter-spacing: 0.5px;
                        }
                    }
    
                    &:after {
                        content: ' ';
                        position: absolute;
                        background: url('/assets/ebooks/desenvolvimento-web-com-python-e-django/arrow-down.png') center center no-repeat;
                        bottom: -16px;
                        left: calc(50% - 18px);
                        width: 36px;
                        height: 16px;
                    }
                }
    
                .form-body {
                    padding: 110px 32px 52px 32px;

                    input {
                        width: 100%;
                        margin-bottom: 24px;
                        padding: 11px;
                        font-size: 13px;
                        border-radius: 5px;
                        border: 2px solid #ccc;
                        font-family: 'Lato', sans-serif;

                        &:focus, &:active {
                            outline: none;
                            border-color: #44b77e;
                        }

                        @media (max-width: 767px) {
                            margin-bottom: 12px;
                        }
                    }
    
                    h3 {
                        font-family: 'Roboto Condensed', sans-serif;
                        font-weight: bold;
                        font-size: 1.45rem;
                        text-transform: uppercase;
                        letter-spacing: 1px;
                        color: #999;
                        margin: 0;
                    }
        
                    h4 {
                        font-family: 'Roboto Condensed', sans-serif;
                        font-weight: bold;
                        font-size: 5rem;
                        color: #44b77e;
                        letter-spacing: -0.3px;
                    }
        
                    button {
                        border: 0;
                        background: #44B78B;
                        border-radius: 8px;                    
                        font-family: 'Lato', sans-serif;
                        text-transform: uppercase;
                        font-weight: bold;                                                
                        color: #FFF;
                        padding: 18px 30px;
                        width: 100%;      
                        
                        @media (max-width: 767px) {
                            padding: 12px 30px;
                        }
                    }

                    .ebook-cover {
                        @media (max-width: 767px) {
                            width: 35%;
                        }

                        @media (max-width: 550px) {
                            width: 65%;
                        }
                    }

                    p.privacy {
                        font-family: 'Lato', sans-serif;
                        font-size: 14px;
                        font-weight: 600;
                        margin-top: 12px;

                        img {
                            margin-right: 6px;
                        }
                    }   
                    
                    #mc_embed_signup {
                        padding: 45px 0;

                        @media (max-width: 767px) {
                            padding: 12px 0;
                        }
                    }

                    @media (max-width: 767px) {
                        padding: 100px 32px 12px 32px;
                    }

                }               
                
                .form-bground {
                    width: 100%;
                    display: block;
                    z-index: -1;
                    position: absolute;
                    bottom: 0;
                    border-radius: 0 0 10px 10px;
                    height: 23vh;
                    opacity: 0.3;
                }

                @media (max-width: 767px) {
                    margin-top: 28px;                    
                }
            }
        }

        @media (max-width: 767px) {
            padding: 70px 16px 16px 16px;
        }
    }
}

.ebook-download-page {
    background-image: url("/assets/svg/stars-bground.svg"), linear-gradient(to bottom, #075638, #01150d);
    padding: 3vh 16px 32px 16px;
    // height: calc(100vh - 80px);

    .download-wrapper {
        width: 100%;
        padding: 32px 24px 20px 24px;
        border-radius: 16px;
        background: #FFF;
        margin-bottom: 32px;
        text-align: center;
        position: relative;
        box-shadow: 0px 6px 24px rgba(0, 0, 0, 0.35);

        h1 {
            font-family: 'Roboto Condensed', sans-serif;
            font-size: 2rem;
            letter-spacing: 0.25px;
            text-transform: uppercase;
            color: #44b77e;
            font-weight: bold;
            margin-top: 0;
            margin-bottom: 0;
            
            img {
                position: relative;
                bottom: 2px;
                left: 2px;
            }
        }

        p {
            font-family: 'Open Sans', sans-serif;
            font-size: 15px;
            margin-bottom: 24px;
        }

        .ebook-cover {
            max-width: 150px;
        }

        .cta {
            display: inline-block;
            border: 0;
            background: #44B78B;
            border-radius: 8px;                    
            font-family: 'Lato', sans-serif;
            text-transform: uppercase;
            font-weight: bold;                                                
            color: #FFF;
            padding: 14px 30px;
            max-width: 400px;
            font-size: 1.5rem;
            margin-bottom: 24px;
            
            @media (max-width: 767px) {
                padding: 12px 30px;
            }
        }

        .share {
            padding: 12px 8px;
            border-radius: 8px;
            background-color: #44b78b1a;
            border: 1px solid #44b78b33;
            font-size: 14px;
            font-family: 'Roboto Condensed', sans-serif;
            font-weight: bold;
            color: #376453;
            display: inline-block;
            width: 100%;
            margin-bottom: 16px;
            transition: all 0.15s ease-in;

            img {
                display: inline-block;
                height: 20px;
                margin-right: 4px;
            }

            &:hover {
                background-color: #44b78b43;
            }
        }
    }

    .whats-next {
        h2 {
            font-family: 'Merriweather', sans-serif;
            font-size: 4rem;
            margin-bottom: 24px;
            font-weight: bold;
            color: #FFF;
        }
    }
}

